import axios from 'axios';
import {
  sideNewsRequest,
  sideNewsSuccessUK,
  sideNewsSuccessRU,
  sideNewsErrorUK,
  sideNewsErrorRU,
} from './sideNewsActions';
import { locale } from '../session/sessionSelectors';

const setAxiosLang = lang => {
  axios.defaults.headers.common['Accept-Language'] = lang;
};

export const onSideNewsOperation = () => (dispatch, getState) => {
  dispatch(sideNewsRequest());

  setAxiosLang(locale(getState()));

  axios
    .get(
      'https://dev2.cybercreation.team/api/news?categoriesId=1,2,3,4&limit=1',
    )
    .then(response => {
      if (locale(getState()) === 'uk')
        dispatch(sideNewsSuccessUK(response.data));
      else {
        dispatch(sideNewsSuccessRU(response.data));
      }
    })
    .catch(error => {
      console.log(error);
      if (locale(getState()) === 'uk') dispatch(sideNewsErrorUK(error));
      else {
        dispatch(sideNewsErrorRU(error));
      }
    });
};
