import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import style from './newsNavMob.module.css';
import * as sessionSelectors from '../../../redux/session/sessionSelectors';

const useStyles = makeStyles(() => ({
  root: {
    flexGrow: 1,
    width: '100%',
    backgroundColor: 'transparent',
    '& .MuiAppBar-colorDefault': {
      backgroundColor: 'transparent',
    },
    '& .MuiPaper-elevation4': {
      boxShadow: 'none',
    },
    '& .MuiSvgIcon-root': {
      fill: '#ffffff',
    },
    '& .MuiTabs-indicator': {
      backgroundColor: '#60B6D9',
    },
    '& .MuiTabs-scrollButtons': {
      width: 25,
    },
    '& .MuiTab-wrapper': {
      height: '100%',
    },
    '& .MuiTab-root': {
      padding: 0,
      maxWidth: 160,
      minWidth: 'unset',
      '&:not(:last-child)': {
        marginRight: 20,
      },
    },
  },
}));

function ScrollableTabsButtonForce(props) {
  const { newsNav = [], locale, allName } = props;
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  useEffect(() => {
    props.history.push(`/${locale}${locale === 'uk' ? '/novini' : '/novosti'}`);
  }, []);

  useEffect(() => {
    props.history.push(`/${locale}${locale === 'uk' ? '/novini' : '/novosti'}`);
  }, [locale]);

  return (
    <div className={classes.root}>
      <AppBar position="static" color="default">
        <Tabs
          value={value}
          onChange={handleChange}
          variant="scrollable"
          scrollButtons="on"
          indicatorColor="primary"
          textColor="primary"
          aria-label="scrollable force tabs example"
        >
          <Tab
            label={
              <Link
                className={style.link}
                to={
                  locale === 'uk' ? `/${locale}/novini` : `/${locale}/novosti`
                }
              >
                {allName}
              </Link>
            }
          />
          {newsNav &&
            newsNav.map(el => (
              <Tab
                key={el.id}
                label={
                  <Link
                    className={style.link}
                    to={
                      locale === 'uk'
                        ? `/${locale}/novini/${el.url}`
                        : `/${locale}/novosti/${el.url}`
                    }
                  >
                    {el.name}
                  </Link>
                }
              />
            ))}
        </Tabs>
      </AppBar>
    </div>
  );
}

const mapStateToProps = state => ({
  locale: sessionSelectors.locale(state),
});

export default connect(mapStateToProps)(withRouter(ScrollableTabsButtonForce));
