import React from 'react';
import MuiExpansionPanel from '@material-ui/core/ExpansionPanel';
import MuiExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import MuiExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import { withStyles } from '@material-ui/core/styles';
import Media from 'react-media';
import style from './accordion.module.css';
import arrow from '../../../../assets/img/icons/arrow-bottom.svg';
import './resetGlobalClassesLoggedOffice.css';
import imgButtonPay from '../../../../assets/img/icons/buttonPay.svg';

const ExpansionPanel = withStyles({
  root: {
    transition: 'color .3s ease',
    fontSize: 20,
    backgroundColor: '#2C4E5B !important',
    boxShadow: 'none !important',
    marginBottom: 'none !important',
    fontFamily: 'Roboto',
    borderRadius: 0,
    position: 'relative',
    '&$expanded': {
      margin: '10px 0',
    },
    '&:not(:last-child)': {
      marginBottom: 10,
    },
    '&:first-child': {
      borderRadius: 0,
    },
    '&:last-child': {
      borderRadius: 0,
    },
  },
  expanded: {},
})(MuiExpansionPanel);

const ExpansionPanelSummary = withStyles({
  root: {
    marginBottom: -1,
    minHeight: 0,
    padding: '0 10px 25px 10px',
    '&:after': {
      content: '""',
      display: 'block',
      width: 15,
      height: 15,
      position: 'absolute',
      zIndex: 999,
      bottom: 5,
      left: '50%',
      transform: 'translate(-50%, 0)',
      backgroundImage: `url(${arrow})`,
      backgroundSize: '15px 15px',
      backgroundPosition: 'center',
      backgroundRepeat: 'no-repeat',
      transition: 'all 0.3s ease',
    },
    '&$expanded': {
      minHeight: 0,
      '&:after': {
        transform: 'translateX(-40%) rotate(180deg)',
      },
    },
  },
  content: {
    margin: '10px 0',
    '&$expanded': {
      margin: '10px 0',
      backgroundColor: 'transparent',
    },
  },
  expanded: {},
})(MuiExpansionPanelSummary);

const ExpansionPanelDetails = withStyles(theme => ({
  root: {
    padding: 0,
    backgroundColor: 'transparent',
  },
}))(MuiExpansionPanelDetails);

export default function Accordion({
  accaunts = [],
  buttonPaied,
  buttonPay,
  bill,
}) {
  const [expanded, setExpanded] = React.useState('panel1');

  const handleChange = panel => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
  };
  return (
    <div className="wrapperLoggedOffice wrapperLoggedOfficeReset">
      {accaunts.map((menuItem, i) => (
        <ExpansionPanel
          key={menuItem.id}
          expanded={expanded === `panel${i + 1}`}
          onChange={handleChange(`panel${i + 1}`)}
        >
          <ExpansionPanelSummary
            aria-controls={`panel${i + 1}d-content`}
            id={`panel${i + 1}d-header`}
          >
            <div className={style.titleWrapper}>
              <div className={style.userInfo}>
                <p className={style.bill}>
                  {bill} {menuItem.id}
                </p>
                <p className={style.address}>{menuItem.address}</p>
                <p className={style.name}>{menuItem.fullName}</p>
              </div>
              <div className={style.paymentWrapper}>
                <div className={style.debtInfo}>
                  {menuItem._balanceType === 'debts' && (
                    <span className={style.debtName}>
                      {menuItem.balanceType}
                    </span>
                  )}
                  {menuItem._balanceType === 'overpay' && (
                    <span className={style.overpayName}>
                      {menuItem.balanceType}
                    </span>
                  )}
                  {menuItem._balanceType === 'even' && (
                    <span className={style.evenName}>
                      {menuItem.balanceType}
                    </span>
                  )}
                  <span className={style.debtSum}>{menuItem.balance}</span>
                </div>
                {menuItem._balanceType === 'debts' && (
                  <a
                    href={menuItem.payLink}
                    target="_blank"
                    className={style.paymentLinkDebts}
                    rel="noopener noreferrer"
                  >
                    <img
                      src={imgButtonPay}
                      className={style.buttonPay}
                      alt="icon pay"
                    />
                    {buttonPay}
                  </a>
                )}
                {menuItem._balanceType === 'overpay' && (
                  <p className={style.paymentLinkOverpay}>{buttonPaied}</p>
                )}
                {menuItem._balanceType === 'even' && (
                  <p className={style.paymentLinkEven}>{buttonPaied}</p>
                )}
              </div>
            </div>
          </ExpansionPanelSummary>
          <ExpansionPanelDetails>
            <ul className={style.innerList}>
              {menuItem.messages.map((message, i) => (
                <li className={style.innerListItem} key={i}>
                  <p className={style.itemText}>{message.text}</p>
                  <Media query={{ minWidth: 1200 }}>
                    {matches =>
                      matches && (
                        <p className={style.messageValue}>{message.value}</p>
                      )
                    }
                  </Media>

                  <a
                    href={message.link}
                    className={style.messageLink}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    {message.btnName}
                  </a>
                </li>
              ))}
            </ul>
          </ExpansionPanelDetails>
        </ExpansionPanel>
      ))}
    </div>
  );
}
