import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import { connect } from 'react-redux';
import { ReactSvgInjector, Mutate } from 'react-svg-injector';
import Media from 'react-media';
import style from './officeNav.module.css';
import citizenryImg from '../../assets/img/icons/lk-citizenry.svg';
import enterpriseImg from '../../assets/img/icons/lk-enterprise.svg';
import pressImg from '../../assets/img/icons/lk-press.svg';
import * as sessionSelectors from '../../redux/session/sessionSelectors';
import * as initDataSelector from '../../redux/initData/initDataSelectors';
import { resetTranslitiration } from '../../redux/translitiration/translitirationActions';

class OfficeNav extends Component {
  componentDidMount = () => {
    const { resetTranslitiration } = this.props;
    resetTranslitiration();
  };

  render() {
    const { linksArrUK = [], linksArrRU = [], locale } = this.props;
    const linksArr = locale === 'uk' ? linksArrUK : linksArrRU;

    return (
      <div className={style.linkBackground}>
        <div className="wrapper">
          {linksArr && (
            <div className={style.linkWrapper}>
              <NavLink
                className={style.link}
                activeClassName={style.linkSelected}
                to={`/${locale}/`}
                exact
              >
                <Media query={{ maxWidth: 1199 }}>
                  {matches =>
                    matches ? (
                      <ReactSvgInjector
                        src={citizenryImg}
                        alt="citizenry icon"
                        className={style.imgLink}
                      >
                        <Mutate selector="g" />
                      </ReactSvgInjector>
                    ) : (
                      <span>{linksArr[0].name}</span>
                    )
                  }
                </Media>
              </NavLink>
              <NavLink
                className={style.link}
                activeClassName={style.linkSelected}
                to={`/${locale}/enterprise`}
              >
                <Media query={{ maxWidth: 1199 }}>
                  {matches =>
                    matches ? (
                      <ReactSvgInjector
                        src={enterpriseImg}
                        alt="enterprise icon"
                        className={style.imgLink}
                      >
                        <Mutate selector="g" />
                      </ReactSvgInjector>
                    ) : (
                      <span>{linksArr[1].name}</span>
                    )
                  }
                </Media>
              </NavLink>
              <NavLink
                className={style.link}
                activeClassName={style.linkSelected}
                to={`/${locale}/press`}
              >
                <Media query={{ maxWidth: 1199 }}>
                  {matches =>
                    matches ? (
                      <ReactSvgInjector
                        src={pressImg}
                        alt="press icon"
                        className={style.imgLink}
                      >
                        <Mutate selector="g" />
                      </ReactSvgInjector>
                    ) : (
                      <span>{linksArr[2].name}</span>
                    )
                  }
                </Media>
              </NavLink>
            </div>
          )}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  locale: sessionSelectors.locale(state),
  linksArrUK: initDataSelector.initDataVidzhetyUK(state),
  linksArrRU: initDataSelector.initDataVidzhetyRU(state),
});

const mapDispatchToProps = {
  resetTranslitiration,
};

export default connect(mapStateToProps, mapDispatchToProps)(OfficeNav);
