import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import RegForm from './regForm/regForm';
import RegFormCorporation from './regForm/regFormCorporation';
import LoggedInfo from './loggedOffice/loggedOffice';
import * as sessionSelectors from '../../redux/session/sessionSelectors';
import * as sessionOperations from '../../redux/session/sessionOperations';
import * as sessionActions from '../../redux/session/sessionActions';
import style from './office.module.css';
import loginErrorImg from '../../assets/img/icons/loginError.svg';

class Office extends Component {
  state = {};

  componentDidMount() {
    if (this.props.locale === 'ru' && !this.props.userRU)
      this.props.refreshUserLK();
    if (this.props.locale === 'uk' && !this.props.userUA)
      this.props.refreshUserLK();
    if (this.props.locale === 'ru' && !this.props.userEnterpriseRU)
      this.props.refreshUserEnterprise();
    if (this.props.locale === 'uk' && !this.props.userEnterpriseUA)
      this.props.refreshUserEnterprise();
  }

  componentDidUpdate(prevProps) {
    if (
      prevProps.locale !== this.props.currelocalentLocale &&
      this.props.locale === 'ru' &&
      !this.props.userRU
    ) {
      this.props.refreshUserLK();
    }
    if (
      prevProps.locale !== this.props.locale &&
      this.props.locale === 'uk' &&
      !this.props.userUA
    ) {
      this.props.refreshUserLK();
    }
    if (
      prevProps.locale !== this.props.locale &&
      this.props.locale === 'ru' &&
      !this.props.userEnterpriseRU
    ) {
      this.props.refreshUserEnterprise();
    }
    if (
      prevProps.locale !== this.props.locale &&
      this.props.locale === 'uk' &&
      !this.props.userEnterpriseUA
    ) {
      this.props.refreshUserEnterprise();
    }
  }

  render() {
    const {
      autorization,
      authenticatedLK,
      authenticatedEnterprise,
      onLogout,
      autorizated,
      locale,
      userUA,
      userRU,
      office,
      userEnterpriseUA,
      userEnterpriseRU,
      onLogoutEnterprise,
      loading,
      loadingEnterprise,
      errorLK,
      errorEnterprise,
      loginErrorLK,
      loginErrorEnterprise,
      setErrorNull,
    } = this.props;
    return (
      <Fragment>
        {office === 'lk' ? (
          <div
            className={
              !authenticatedLK ? style.officeWrapper : style.officeWrapperLogged
            }
          >
            {!authenticatedLK ? (
              <Fragment>
                <h2 className={style.loggingHeader}>{autorization.header}</h2>
                {loginErrorLK && (
                  <div onClick={setErrorNull} className={style.errorWrapper}>
                    <img
                      className={style.errorIcon}
                      src={loginErrorImg}
                      alt="error icon"
                    />
                    <p className={style.errorText}>
                      {(loginErrorLK.data && loginErrorLK.data.text) ||
                        'Some network error'}
                    </p>
                  </div>
                )}
                <RegForm
                  setErrorNull={setErrorNull}
                  {...autorization}
                  currentLocale={locale}
                  office={office}
                />
              </Fragment>
            ) : (
              <LoggedInfo
                {...autorizated}
                onLogout={onLogout}
                user={locale === 'ru' ? userRU : userUA}
                loading={loading}
                currentLocale={locale}
                error={errorLK}
              />
            )}
          </div>
        ) : (
          <div
            className={
              !authenticatedEnterprise
                ? style.officeWrapper
                : style.officeWrapperLogged
            }
          >
            {!authenticatedEnterprise ? (
              <Fragment>
                <h2 className={style.loggingHeader}>{autorization.header}</h2>
                {loginErrorEnterprise && (
                  <div onClick={setErrorNull} className={style.errorWrapper}>
                    <img
                      className={style.errorIcon}
                      src={loginErrorImg}
                      alt="error icon"
                    />
                    <p className={style.errorText}>
                      {(loginErrorEnterprise.data &&
                        loginErrorEnterprise.data.text) ||
                        'Some network error'}
                    </p>
                  </div>
                )}
                <RegFormCorporation
                  setErrorNull={setErrorNull}
                  {...autorization}
                  currentLocale={locale}
                  office={office}
                />
              </Fragment>
            ) : (
              <LoggedInfo
                {...autorizated}
                onLogout={onLogoutEnterprise}
                user={locale === 'ru' ? userEnterpriseRU : userEnterpriseUA}
                loading={loadingEnterprise}
                currentLocale={locale}
                error={errorEnterprise}
              />
            )}
          </div>
        )}
      </Fragment>
    );
  }
}

const mapStateToProps = state => ({
  authenticatedLK: sessionSelectors.getIsAuthenticatedLK(state),
  authenticatedEnterprise: sessionSelectors.getIsAuthenticatedEnterprise(state),
  userUA: sessionSelectors.getUserLKUA(state),
  userRU: sessionSelectors.getUserLKRU(state),
  userEnterpriseRU: sessionSelectors.getUserEnterpriseRU(state),
  userEnterpriseUA: sessionSelectors.getUserEnterpriseUA(state),
  loading: sessionSelectors.loading(state),
  loadingEnterprise: sessionSelectors.loadingEnterprise(state),
  errorLK: sessionSelectors.getErrorLK(state),
  errorEnterprise: sessionSelectors.getErrorEnterprise(state),
  loginErrorLK: sessionSelectors.getLoginErrorLK(state),
  loginErrorEnterprise: sessionSelectors.getLoginErrorEnterprise(state),
  locale: sessionSelectors.locale(state),
});

const mapDispatchToProps = {
  onLogout: sessionOperations.logOutOperation,
  onLogoutEnterprise: sessionOperations.logOutOperationEnterpise,
  refreshUserLK: sessionOperations.refreshUserLK,
  refreshUserEnterprise: sessionOperations.refreshUserEnterprise,
  setErrorNull: sessionActions.setErrorNull,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Office);
