import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import style from './sideNews.module.css';
import * as sessionSelectors from '../../../redux/session/sessionSelectors';
import * as sideNewsSelectors from '../../../redux/sideNews/sideNewsSelectors';
import * as sideNewsOperation from '../../../redux/sideNews/sideNewsOperations';

const length = someString => {
  if (someString.length <= 37) return someString;
  return `${someString.slice(0, 38)}...`;
};

const largeLength = someString => {
  if (someString.length <= 200) return someString;
  return `${someString.slice(0, 201)}...`;
};

const foo = obj => {
  const newArr = [];
  Object.values(obj).forEach(el => {
    if (!Array.isArray(el[0])) newArr.push(el[0]);
  });
  return newArr;
};

class SideNews extends Component {
  state = {};

  componentDidMount = () => {
    const { onInitData } = this.props;
    if (
      (this.props.locale === 'uk' && !this.props.sideNewsUK) ||
      (this.props.locale === 'ru' && !this.props.sideNewsRU)
    )
      onInitData();
  };

  componentDidUpdate = prevProps => {
    const { onInitData } = this.props;
    if (
      prevProps.locale !== this.props.locale &&
      this.props.locale === 'ru' &&
      !this.props.sideNewsRU
    ) {
      onInitData();
    }
    if (
      prevProps.locale !== this.props.locale &&
      this.props.locale === 'uk' &&
      !this.props.sideNewsUK
    ) {
      onInitData();
    }
  };

  render() {
    const { locale, sideNewsUK, sideNewsRU } = this.props;
    const sideNews = locale === 'uk' ? sideNewsUK : sideNewsRU;
    return (
      <ul className={style.sideList}>
        {sideNews &&
          foo(sideNews).map(newsEl => (
            <li className={style.sideListItem} key={newsEl.id}>
              <h3 className={style.sideListItemTitle}>{newsEl.category}</h3>
              <div className={style.newsWrapper}>
                <picture>
                  <source srcSet={newsEl.image} type="image/webp" />
                  <source srcSet={newsEl.image} type="image/jp2" />
                  <source srcSet={newsEl.image} type="image/jpg" />
                  <img
                    className={style.imgBack}
                    src={newsEl.image}
                    alt="news img"
                  />
                </picture>
                <div className={style.date}>{newsEl.published}</div>
                <div className={style.newsBlock}>
                  <p className={style.newsHead}>{length(newsEl.title)}</p>
                  <p className={style.content}>{largeLength(newsEl.brief)}</p>
                  <div className={style.link}>
                    <i className="material-icons">keyboard_backspace</i>
                  </div>
                </div>
                <Link
                  className={style.linkLink}
                  to={`/${locale}${newsEl.url}`}
                />
              </div>
            </li>
          ))}
      </ul>
    );
  }
}

const mapStateToProps = state => ({
  locale: sessionSelectors.locale(state),
  sideNewsUK: sideNewsSelectors.sideNewsUK(state),
  sideNewsRU: sideNewsSelectors.sideNewsRU(state),
});

const mapDispatchToProps = {
  onInitData: sideNewsOperation.onSideNewsOperation,
};

export default connect(mapStateToProps, mapDispatchToProps)(SideNews);
