import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import style from './newsListMob.module.css';
import * as sessionSelectors from '../../../redux/session/sessionSelectors';

const NewsListMob = ({ news = [], locale }) => (
  <Fragment>
    {news.length > 0 && (
      <ul className={style.list}>
        {news.map(item => (
          <li className={style.listItem} key={item.id}>
            <picture>
              <source srcSet={item.image} type="image/webp" />
              <source srcSet={item.image} type="image/jp2" />
              <source srcSet={item.image} type="image/jpg" />
              <img className={style.imgBack} src={item.image} alt="news img" />
            </picture>
            <Link className={style.link} to={`/${locale}${item.url}`} />
            <div className={style.date}>{item.published}</div>
            <p className={style.newsHead}>{item.title}</p>
          </li>
        ))}
      </ul>
    )}
    {!news.length && (
      <h2 className={style.error}>Not found news by this category</h2>
    )}
  </Fragment>
);

const mapStateToProps = state => ({
  locale: sessionSelectors.locale(state),
});

export default connect(mapStateToProps)(NewsListMob);
