import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import style from './newsListMob.module.css';
import * as sessionSelectors from '../../../redux/session/sessionSelectors';
import * as startPageNewsSelectors from '../../../redux/startPageIndex/startPageIndexSelectors';
import * as onStartPageInit from '../../../redux/startPageIndex/startPageIndexOperations';

class NewsListMob extends Component {
  state = {};

  componentDidMount = () => {
    const { onInitData } = this.props;
    if (
      (this.props.locale === 'uk' && !this.props.newsListUK) ||
      (this.props.locale === 'ru' && !this.props.newsListRU)
    )
      onInitData();
  };

  componentDidUpdate = prevProps => {
    const { onInitData } = this.props;
    if (
      prevProps.locale !== this.props.locale &&
      this.props.locale === 'ru' &&
      !this.props.newsListRU
    ) {
      onInitData();
    }
    if (
      prevProps.locale !== this.props.locale &&
      this.props.locale === 'uk' &&
      !this.props.newsListUK
    ) {
      onInitData();
    }
  };

  render() {
    const { newsListRU = [], newsListUK = [], locale } = this.props;
    const news = locale === 'uk' ? newsListUK : newsListRU;
    if (news) news.length = 4;
    return (
      <ul className={style.list}>
        {news &&
          news.map(item => (
            <li className={style.listItem} key={item.id}>
              <picture>
                <source srcSet={item.icon} type="image/webp" />
                <source srcSet={item.icon} type="image/jp2" />
                <source srcSet={item.icon} type="image/jpg" />
                <img className={style.imgBack} src={item.icon} alt="news img" />
              </picture>
              <Link className={style.link} to={`/${locale}${item.url}`} />
              <div className={style.date}>{item.published}</div>
              <p className={style.newsHead}>{item.title}</p>
            </li>
          ))}
      </ul>
    );
  }
}

const mapStateToProps = state => ({
  locale: sessionSelectors.locale(state),
  newsListRU: startPageNewsSelectors.startPageNewsRU(state),
  newsListUK: startPageNewsSelectors.startPageNewsUK(state),
});
const mapDispatchToProps = {
  onInitData: onStartPageInit.onStartPageNewsOperation,
};

export default connect(mapStateToProps, mapDispatchToProps)(NewsListMob);
