import React, { Component, Fragment } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import Localization from '../../context/localization';
import MapControl from '../../components/mapControl/mapControl';
import styles from './startPage.module.css';
import NewsListMain from '../../components/newsIndex/newsIndex';
import NewsCategoriesPage from '../newsCategoriesPage/newsCategoriesPage';
import Footer from '../../components/footer/footer';
import SinglePageArticle from '../singleArticle/singleArticle';
import InProgress from '../errorPage/errorPage';
import CompanyHistory from '../singlePublication/companyHistory';
import SingleArticle from '../singlePublication/singlePublication';
import * as sessionSelectors from '../../redux/session/sessionSelectors';
import Header from '../../components/header/header';
import PageWithOffice from '../pageWithOffice/pageWithOffice';
import PageWithOfficePress from '../pageWithPress/pageWithPress';
import * as newsNavSelectors from '../../redux/news/newsNav/newsNavSelectors';

class StartPage extends Component {
  state = {};

  render() {
    const {
      autorization,
      header,
      currentLocale,
      quickMenu,
      map,
      footer,
      newsMain,
      newsContent,
      singleNews,
      history,
      locale,
    } = this.props;
    return (
      <Fragment>
        <Header {...header} currentLocale={currentLocale} />
        <Switch>
          <Route
            exact
            path={`/${locale.locale}/`}
            render={() => (
              <PageWithOffice
                currentLocale={currentLocale}
                autorization={autorization.officeLK}
                autorizated={autorization.loggedOffice}
                office="lk"
              />
            )}
          />
          <Route
            path={[`/${locale.locale}/golovna`, `/${locale.locale}/glavnaya`]}
            exact
            render={() => {
              return <Redirect to={`/${locale.locale}/`} />;
            }}
          />
          <Route
            path={`/${locale.locale}/enterprise`}
            render={() => (
              <PageWithOffice
                currentLocale={currentLocale}
                autorization={autorization.officeEnterprise}
                autorizated={autorization.loggedOffice}
                office="enterprise"
              />
            )}
          />
          <Route
            path={`/${locale.locale}/press`}
            render={() => (
              <PageWithOfficePress
                press={quickMenu.press}
                currentLocale={currentLocale}
              />
            )}
          />
        </Switch>
        <div className={styles.contentBlock}>
          <Switch>
            <Route
              exact
              path={[
                `/${locale.locale}/`,
                `/${locale.locale}/enterprise`,
                `/${locale.locale}/press`,
              ]}
              render={() => (
                <Fragment>
                  <MapControl {...map} currentLocale={currentLocale} />
                  <NewsListMain currentLocale={currentLocale} {...newsMain} />
                </Fragment>
              )}
            />
            {/* <Route
              path={[
                `/${locale.locale}/novini/tarifi/:id`,
                `/${locale.locale}/novini/remonti/:id`,
                `/${locale.locale}/novini/podiyi/:id`,
                `/${locale.locale}/novini/povidomlennia/:id`,
              ]}
              render={() => (
                <SinglePageArticle
                  currentLocale={currentLocale}
                  {...singleNews}
                />
              )}
            /> */}
            {locale.newsNavUK &&
              locale.newsNavUK.map(el => (
                <Route
                  key={el.id}
                  path={`/${locale.locale}/novini/${el.url}/:id`}
                  render={() => (
                    <SinglePageArticle
                      currentLocale={currentLocale}
                      {...singleNews}
                    />
                  )}
                />
              ))}
            {locale.newsNavRU &&
              locale.newsNavRU.map(el => (
                <Route
                  key={el.id}
                  path={`/${locale.locale}/novosti/${el.url}/:id`}
                  render={() => (
                    <SinglePageArticle
                      currentLocale={currentLocale}
                      {...singleNews}
                    />
                  )}
                />
              ))}
            <Route
              path={[`/${locale.locale}/novini`, `/${locale.locale}/novosti`]}
              render={() => (
                <NewsCategoriesPage
                  currentLocale={currentLocale}
                  newsContent={newsContent}
                />
              )}
            />
            <Route
              path={[
                `/${locale.locale}/info/pro-pidpriemstvo/istoriia`,
                `/${locale.locale}/info/pro-predpriiatie/istoriia`,
              ]}
              render={() => (
                <CompanyHistory
                  currentLocale={currentLocale}
                  {...history}
                  {...singleNews}
                />
              )}
            />
            <Route
              path={`/${locale.locale}/info/:publication`}
              render={() => (
                <SingleArticle currentLocale={currentLocale} {...singleNews} />
              )}
            />
            <Route render={() => <InProgress />} />
          </Switch>
        </div>
        <Footer currentLocale={currentLocale} footer={footer} />
      </Fragment>
    );
  }
}

const mapStateToProps = state => ({
  locale: sessionSelectors.locale(state),
  newsNavUK: newsNavSelectors.newsNavUK(state),
  newsNavRU: newsNavSelectors.newsNavRU(state),
});

function LocalizedStartPage(locale) {
  return (
    <Localization.Consumer>
      {({ localizedContent, currentLocale }) => (
        <StartPage
          mapStateToProps
          locale={locale}
          {...localizedContent}
          currentLocale={currentLocale}
        />
      )}
    </Localization.Consumer>
  );
}

export default connect(mapStateToProps)(LocalizedStartPage);
