import React, { Component, Fragment } from 'react';
import { Switch, Route } from 'react-router-dom';
import { connect } from 'react-redux';
import AllNews from './allNews';
import CategoryNews from './newsCategiries';
import NewsDate from './newsDate';
import * as sessionSelectors from '../../redux/session/sessionSelectors';
import {
  newsNavUK,
  newsNavRU,
} from '../../redux/news/newsNav/newsNavSelectors';

class BuildingNewsList extends Component {
  componentDidMount = () => {
    window.scrollTo(0, 0);
  };

  render() {
    const { button, locale, newsNavUK, newsNavRU } = this.props;

    return (
      <Fragment>
        <Switch>
          {locale === 'uk'
            ? newsNavUK &&
              newsNavUK.map(route => (
                <Route
                  key={route.id}
                  path={`/${locale}/novini/${route.url}`}
                  render={() => (
                    <CategoryNews button={button} routeNumber={route.id} />
                  )}
                />
              ))
            : newsNavRU &&
              newsNavRU.map(route => (
                <Route
                  key={route.id}
                  path={`/${locale}/novosti/${route.url}`}
                  render={() => (
                    <CategoryNews button={button} routeNumber={route.id} />
                  )}
                />
              ))}
          <Route
            path={[
              `/${locale}/novini/date/:date`,
              `/${locale}/novosti/date/:date`,
            ]}
            render={() => <NewsDate button={button} />}
          />
          <Route
            exact
            path={[`/${locale}/novini`, `/${locale}/novosti`]}
            render={() => <AllNews button={button} />}
          />
        </Switch>
      </Fragment>
    );
  }
}

const mapStateToProps = state => ({
  locale: sessionSelectors.locale(state),
  newsNavUK: newsNavUK(state),
  newsNavRU: newsNavRU(state),
});

export default connect(mapStateToProps)(BuildingNewsList);
