import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import style from './press.module.css';
import * as sessionSelectors from '../../redux/session/sessionSelectors';
import * as initDataSelector from '../../redux/initData/initDataSelectors';

class Press extends Component {
  componentDidMount = () => {};

  render() {
    const { linksArrUK = [], linksArrRU = [], locale } = this.props;
    const press = locale === 'uk' ? linksArrUK : linksArrRU;
    return (
      <div className="wrapper">
        <ul className={style.list}>
          {press &&
            press[2] &&
            press[2].children &&
            press[2].children.length > 0 &&
            press[2].children.map(el => (
              <li className={style.listItem} key={el.id}>
                <div className={style.background} />
                <Link
                  className={style.link}
                  to={el.url ? `/${locale}${el.url}` : `/${locale}/`}
                >
                  <img className={style.cardImg} src={el.icon} alt={el.title} />
                  <span className={style.cardName}>{el.name}</span>
                </Link>
              </li>
            ))}
        </ul>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  locale: sessionSelectors.locale(state),
  linksArrUK: initDataSelector.initDataVidzhetyUK(state),
  linksArrRU: initDataSelector.initDataVidzhetyRU(state),
});

export default connect(mapStateToProps)(Press);
