import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import Media from 'react-media';
import style from './newsIndex.module.css';
import newsList from '../../bd/newsList.json';
import NewsListMob from './newsListMob/newsListMob';
import NewsListDesk from './newsListDesk/newsListDesk';
import * as sessionSelectors from '../../redux/session/sessionSelectors';

const NewsContainer = ({ header, button, currentLocale, locale }) => (
  <div className="wrapper">
    <div className={style.wrapper}>
      <h3 className={style.newsHeader}>{header}</h3>
      <Media query={{ maxWidth: 1199 }}>
        {matches =>
          matches ? (
            <NewsListMob
              news={currentLocale === 'ru' ? newsList.newsRu : newsList.newsUk}
            />
          ) : (
            <NewsListDesk
              news={currentLocale === 'ru' ? newsList.newsRu : newsList.newsUk}
            />
          )
        }
      </Media>
      <Link
        className={style.button}
        to={locale === 'uk' ? `/${locale}/novini` : `/${locale}/novosti`}
      >
        {button}
      </Link>
    </div>
  </div>
);

const mapStateToProps = state => ({
  locale: sessionSelectors.locale(state),
});

export default connect(mapStateToProps)(NewsContainer);
