export const loginEnterpise = credentials => (dispatch, getState) => {
  dispatch(loginRequestEnterprise());

  const lang = locale(getState());

  setAxiosLang(lang);

  axios.defaults.baseURL = 'http://api-info.dvlp/cent';

  axios({
    method: 'post',
    url: '/attempt',
    data: {
      login: credentials.mail,
      password: credentials.password,
    },
  })
    .then(response => {
      if (response.data.link) window.open(response.data.link, '_blank');
    })
    .catch(error => {
      clearAuthToken();
      if (error.response) {
        dispatch(loginErrorEnterprise(error.response));
      } else {
        dispatch(loginErrorEnterprise(error));
      }
    });
};

export const logOutOperation = () => dispatch => {
  clearAuthToken();
  dispatch(logOut());
};

export const logOutOperationEnterpise = () => dispatch => {
  clearAuthTokenEnterprise();
  dispatch(logOutEnterprise());
};

export const refreshUserLK = () => (dispatch, getState) => {
  const token = getTokenLK(getState());
  if (!token) return;
  const decoded = jwt(token).iat * 1000;
  if (decoded < Date.now()) {
    logOutOperation();
    return;
  }
  dispatch(refreshSuccess());
  const localeLang = locale(getState());
  if (localeLang === 'uk') getDataUA(token, dispatch);
  else getDataRU(token, dispatch);
};

export const refreshUserEnterprise = () => (dispatch, getState) => {
  const token = getTokenEnterprise(getState());
  if (!token) return;
  const decoded = jwt(token).iat * 1000;
  if (decoded < Date.now()) {
    logOutOperationEnterpise();
    return;
  }
  dispatch(refreshSuccessEnterprise());
  const localeLang = locale(getState());
  if (localeLang === 'uk') getDataUAEnterprise(token, dispatch);
  else getDataRUEnterprise(token, dispatch);
};
