import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import style from './newsListDesk.module.css';
import * as sessionSelectors from '../../../redux/session/sessionSelectors';

const length = someString => {
  try {
    if (someString.length <= 37) return someString;
    return `${someString.slice(0, 38)}...`;
  } catch (error) {}
};

const largeLength = someString => {
  try {
    if (someString.length <= 200) return someString;
    return `${someString.slice(0, 201)}...`;
  } catch (error) {}
};

const NewsListDesk = ({ news = [], locale }) => {
  return (
    <Fragment>
      {news.length > 0 && (
        <ul className={style.list}>
          {news.map(item => (
            <li className={style.listItem} key={item.id}>
              <picture>
                <source srcSet={item.image} type="image/webp" />
                <source srcSet={item.image} type="image/jp2" />
                <source srcSet={item.image} type="image/jpg" />
                <img
                  className={style.imgBack}
                  src={item.image}
                  alt="news img"
                />
              </picture>
              <div className={style.date}>{item.published}</div>
              <div className={style.newsBlock}>
                <p className={style.newsHead}>{length(item.title)}</p>
                <p className={style.content}>{largeLength(item.brief)}</p>
                <div className={style.link}>
                  <i className="material-icons">keyboard_backspace</i>
                </div>
              </div>
              <Link className={style.linkLink} to={`/${locale}${item.url}`} />
            </li>
          ))}
        </ul>
      )}
      {!news.length && (
        <h2 className={style.error}>Not found news by this category</h2>
      )}
    </Fragment>
  );
};

const mapStateToProps = state => ({
  locale: sessionSelectors.locale(state),
});

export default connect(mapStateToProps)(NewsListDesk);
