import React from 'react';
import Media from 'react-media';
import MuiExpansionPanel from '@material-ui/core/ExpansionPanel';
import MuiExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import MuiExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import { withStyles } from '@material-ui/core/styles';
import { ReactSvgInjector, Mutate } from 'react-svg-injector';
import { connect } from 'react-redux';
import style from './quickMenuPlusForm.module.css';
import Office from '../office/office';
import MenuListDesk from './menuListDesk/menuListDesk';
import arrow from '../../assets/img/icons/arrow-right-copy.svg';
import './resetGlobalClassesQuickMenu.css';
import * as sessionSelectors from '../../redux/session/sessionSelectors';
import * as initDataSelectors from '../../redux/initData/initDataSelectors';

const Arrow = () => (
  <ReactSvgInjector src={arrow} className={style.arrow}>
    <Mutate selector="g" />
  </ReactSvgInjector>
);

const selectIndex = (quickMenu, office) => {
  return office === 'lk' ? quickMenu[0].children : quickMenu[1].children;
};

const ExpansionPanel = withStyles({
  root: {
    color: 'rgba(255, 255, 255, 0.5)',
    transition: 'color .3s ease',
    fontSize: 20,
    backgroundXolor: 'none !important',
    boxShadow: 'none !important',
    marginBottom: 'none !important',
    fontFamily: 'PT Sans',
    borderRadius: 3,
    '&:before': {
      display: 'none',
    },
    '&$expanded': {
      margin: 'auto',
      color: 'rgba(255, 255, 255, 1)',
    },
    '&:not(:last-child)': {
      marginBottom: 10,
    },
  },
  expanded: {},
})(MuiExpansionPanel);

const ExpansionPanelSummary = withStyles({
  root: {
    marginBottom: -1,
    minHeight: 0,
    '&$expanded': {
      minHeight: 0,
    },
  },
  content: {
    '&$expanded': {
      margin: '12px 0',
      backgroundColor: 'transparent',
    },
  },
  expanded: {},
})(MuiExpansionPanelSummary);

const ExpansionPanelDetails = withStyles(theme => ({
  root: {
    padding: 0,
    backgroundColor: 'transparent',
  },
}))(MuiExpansionPanelDetails);

function QuickMenuPlusForm({
  linksArrayUK = [],
  linksArrayRU = [],
  autorization,
  autorizated,
  office,
  locale,
}) {
  const [expanded, setExpanded] = React.useState('panel1');

  const handleChange = panel => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
  };

  const quickMenu = locale === 'uk' ? linksArrayUK : linksArrayRU;
  return (
    <div className="quickMenu">
      <div className="wrapper">
        <div className={style.grid}>
          <div className={style.quickMenuWrapper}>
            {quickMenu && (
              <Media query={{ maxWidth: 1199 }}>
                {matches =>
                  matches
                    ? selectIndex(quickMenu, office).map((menuItem, i) => (
                        <ExpansionPanel
                          key={menuItem.id}
                          expanded={expanded === `panel${i + 1}`}
                          onChange={handleChange(`panel${i + 1}`)}
                        >
                          <ExpansionPanelSummary
                            aria-controls={`panel${i + 1}d-content`}
                            id={`panel${i + 1}d-header`}
                          >
                            <h3 className={style.menuItemTitle}>
                              {menuItem.name} <Arrow />
                            </h3>
                          </ExpansionPanelSummary>
                          <ExpansionPanelDetails>
                            <MenuListDesk list={menuItem.children} />
                          </ExpansionPanelDetails>
                        </ExpansionPanel>
                      ))
                    : selectIndex(quickMenu, office).map(menuItem => (
                        <div
                          key={menuItem.id}
                          className={style.menuItemWrapper}
                        >
                          <div className={style.menuItemIconWrapper}>
                            <h3 className={style.menuItemTitle}>
                              {menuItem.name}
                            </h3>
                            <MenuListDesk list={menuItem.children} />
                          </div>
                        </div>
                      ))
                }
              </Media>
            )}
          </div>
          <div className={style.officeWrapper}>
            <Office
              autorizated={autorizated}
              autorization={autorization}
              office={office}
            />
          </div>
        </div>
      </div>
    </div>
  );
}

const mapStateToProps = state => ({
  locale: sessionSelectors.locale(state),
  linksArrayUK: initDataSelectors.initDataVidzhetyUK(state),
  linksArrayRU: initDataSelectors.initDataVidzhetyRU(state),
});

export default connect(mapStateToProps)(QuickMenuPlusForm);
