import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import style from './menuListDesk.module.css';
import * as sessionSelectors from '../../../redux/session/sessionSelectors';

const MenuListDesk = ({ list = [], locale }) => (
  <ul className={style.list}>
    {list.map(item => (
      <li className={style.listItem} key={item.id}>
        <Link
          className={style.link}
          to={item.url ? `/${locale}${item.url}` : '/'}
        >
          {item.name}
        </Link>
      </li>
    ))}
  </ul>
);

const mapStateToProps = state => ({
  locale: sessionSelectors.locale(state),
});

export default connect(mapStateToProps)(MenuListDesk);
