import React from 'react';
import { ReactSvgInjector, Mutate } from 'react-svg-injector';
import { RotateSpinLoader } from 'react-css-loaders';
import style from './loggedOffice.module.css';
import userIcon from '../../../assets/img/icons/icon_user.svg';
import officeExit from '../../../assets/img/icons/officeExit.svg';
import Accordion from './accordion/accordion';

const LoggedInfo = ({
  onLogout,
  homeLink,
  exit,
  user,
  loading,
  error,
  buttonPay,
  buttonPaied,
  bill,
}) => {
  let name;
  let email;
  // let avatar;
  let accaunts = [];
  if (user) {
    name = user.profile.name;
    email = user.profile.email;
    // avatar = user.profile.avatar;
    accaunts = user.accaunts;
  }
  return (
    <div className={style.wrapper}>
      <div className={style.upperUserWrapper}>
        <div className={style.userWrapper}>
          {loading ? (
            <div className={style.emptyImg} />
          ) : (
            <img
              // src={avatar || userIcon}
              src={userIcon}
              className={style.userIcon}
              alt="User Icon"
            />
          )}
          <div className={style.userTextWrapper}>
            {!loading && (
              <span className={style.userName}>{name || ''}</span>
            )}
            <span className={style.userMail}>{email}</span>
          </div>
        </div>
        <a
          href="https://cpp.infoxvod.com.ua/"
          target="_blank"
          rel="noopener noreferrer"
          className={style.homeLink}
        >
          {homeLink}
        </a>
      </div>
      <div className={style.data}>
        {loading && (
          <div className={style.loader}>
            <RotateSpinLoader />
          </div>
        )}
        {accaunts.length > 0 && (
          <Accordion
            buttonPay={buttonPay}
            buttonPaied={buttonPaied}
            bill={bill}
            accaunts={accaunts}
          />
        )}
        {error && (
          <h2
            style={{
              fontSize: 36,
              textAlign: 'center',
              lineHeight: 1.5,
            }}
          >
            {error.message}
          </h2>
        )}
      </div>
      <div className={style.exitWrapper}>
        <button className={style.exit} onClick={onLogout}>
          {exit}
          <ReactSvgInjector
            src={officeExit}
            alt="Exit Icon"
            className={style.officeExit}
          >
            <Mutate selector="g" />
          </ReactSvgInjector>
        </button>
      </div>
    </div>
  );
};

export default LoggedInfo;
